raul-menu-dropdown {
  opacity: 0;
  transition: opacity .15s linear;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999;
}

raul-menu-dropdown .r-menu-dropdown {
  opacity: 0;
  transition: opacity .15s linear;
  border-radius: 0.125rem;
  box-shadow: 0 8px 16px 0 rgba(82, 97, 115, 0.18);
  background-color: #fff;
}

raul-menu-dropdown .r-menu-dropdown--links {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

raul-menu-dropdown .r-menu-dropdown--links .r-menu-dropdown__item {
  color: #0076CC;
}

raul-menu-dropdown .r-menu-dropdown--items .r-menu-dropdown__item {
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  color: #202737;
}

body[modality=keyboard] raul-menu-dropdown .r-menu-dropdown--items .r-menu-dropdown__button:focus {
  outline: none;
}

body[modality=keyboard] raul-menu-dropdown .r-menu-dropdown--items .r-menu-dropdown__button:focus raul-icon {
  color: #0076CC;
}

body[modality=keyboard] raul-menu-dropdown .r-menu-dropdown:focus {
  outline: none;
}

body[modality=keyboard] raul-menu-dropdown .r-menu-dropdown .r-menu-dropdown__item:focus {
  outline: 1px solid #0076CC;
}

raul-menu-dropdown .r-menu-dropdown .r-menu-dropdown__item--border {
  border-width: 1px;
  border-left-width: 0;
  border-right-width: 0;
  border-top-width: 0;
  border-color: #EBEDEE;
}

raul-menu-dropdown .r-menu-dropdown .r-menu-dropdown__item--border:last-child {
  border-bottom-width: 0;
}

raul-menu-dropdown .r-menu-dropdown__button {
  padding-left: 1rem;
  padding-right: 1rem;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: left;
  position: relative;
  display: block;
  line-height: 2rem;
}

raul-menu-dropdown .r-menu-dropdown__button--destructive {
  color: #D01A1F;
}

raul-menu-dropdown .r-menu-dropdown__button--disabled {
  color: #9BA3A7;
  pointer-events: none;
}

raul-menu-dropdown .r-menu-dropdown__button--icon raul-icon {
  padding-right: 0.5rem;
}

raul-menu-dropdown .r-menu-dropdown__button--underline:hover {
  text-decoration: underline;
}

body[modality=keyboard] raul-menu-dropdown .r-menu-dropdown__button:focus {
  outline: 1px solid #0076CC;
}

raul-menu-dropdown .r-menu-dropdown__button raul-icon {
  margin-right: 0.5rem;
  margin-top: -2px;
  font-size: inherit;
}

raul-menu-dropdown .r-menu-dropdown__spacer::before {
  border-top-width: 1px;
  border-color: #F7F8F9;
  content: "";
  display: block;
  width: 100%;
  margin-top: 8px;
  margin-bottom: 8px;
}

raul-menu-dropdown.r-menu-dropdown--open {
  opacity: 1;
}

raul-menu-dropdown.r-menu-dropdown--open .r-menu-dropdown {
  opacity: 1;
}